.login {
  width: 100%;
  height: 100%;
  background-image: url(/static/media/bg.489db875.jpg);
  background-size: 100% 100%;
}
.login .login-header {
  display: flex;
  width: 100%;
  height: 80px;
  background-color: rgba(0, 0, 0, 0.2);
  align-items: center;
}
.login .login-header img {
  width: 40px;
  height: 40px;
  margin: 0 15px 0 50px;
}
.login .login-header h1 {
  font-weight: normal;
  font-size: 20px;
  color: #ffffff;
}
.login .login-content {
  width: 400px;
  height: 300px;
  background-color: #fff;
  margin: 150px auto;
  padding: 20px 40px;
  position: relative;
}
.login .login-content .error-msg {
  visibility: hidden;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  text-align: center;
  color: #ffffff;
  background-color: #f60c1a;
  font-size: 16px;
  -webkit-transform: translateY(-30px);
          transform: translateY(-30px);
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.login .login-content .error-msg.show {
  visibility: visible;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.login .login-content h2 {
  height: 50px;
  line-height: 50px;
  text-align: center;
  font-size: 25px;
  font-weight: bold;
  margin-bottom: 20px;
}
.login .login-content .login-form .login-form-button {
  width: 100%;
}
